


import Vue from 'vue';
import shopdetail from './shopdetail';

import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(ToastPlugin);
Vue.use(ConfirmPlugin);
Vue.use(LoadingPlugin);

import axios from 'axios';
axios.defaults.headers.post['Content-Type'] = 'application/json'; 
Vue.prototype.axios = axios;


import '../../common/stylus/index.styl';

import store from '../../store';

import router from './router/index.js';


import VueLazyload from 'vue-lazyload';
Vue.use(VueLazyload, {
error: store.state.global.hftcom + '/huiquan/static/img/null-page-draw.png', // 这个是请求失败后显示的图片
loading: store.state.global.hftcom + '/huiquan/static/img/loading.gif', // 这个是加载的loading过渡效果
try: 3 // 这个是加载图片数量
});


import infiniteScroll from 'vue-infinite-scroll';
Vue.use(infiniteScroll);




require('swiper/dist/css/swiper.css');

new Vue({
template: '<App/>',
components: { shopdetail },
router,
store
}).$mount('#shopdetail');



